import supabase from '~/utils/supabase';
import { useEffect } from 'react';
import { getSession, destroySession } from '~/utils/cookie';
import { redirect, useFetcher } from 'remix';

export const action = async ({ request }) => {
  const session = await getSession(request.headers.get('Cookie'));
  const cookie = await destroySession(session);

  return redirect('/login', {
    headers: {
      'Set-Cookie': cookie,
    },
  });
};

export default () => {
  const fetcher = useFetcher();
  useEffect(() => {
    const logout = async () => {
      await supabase.auth.signOut();
      fetcher.submit(null, {
        method: 'post',
        action: '/logout',
      });
    };
    logout();
  }, []);
  return (
    <div className="h-screen flex items-center justify-center bg-gray-800 text-white">
      <svg
        className="animate-spin -ml-1 mr-3 h-8 w-8 text-white"
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        viewBox="0 0 24 24"
      >
        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
        <path
          className="opacity-75"
          fill="currentColor"
          d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
        ></path>
      </svg>
      <h1 className="text-3xl">Signing you out...</h1>
    </div>
  );
};
